const OneQuestionForm = ({ validateResponse, question }) => {
  const [error, setError] = useState(false);
  const [input, setInput] = useState("");
  const handleChangeInput = (e) => {
    setError(false);
    setInput(e.target.value);
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    console.log();
    if (validateResponse(input)) {
      setError(false);
    } else {
      setError(true);
    }
  };
  return (
    <form onSubmit={handleSubmit}>
      <label htmlFor="text" className="block text-lg ">
        {question}
      </label>
      <div className="mt-1 relative rounded-md shadow-sm">
        <input
          type="text"
          name="text"
          id="text"
          className="block w-full pr-10 border-red-300 text-red-900 placeholder-red-300 focus:outline-none focus:ring-red-500 focus:border-red-500 sm:text-sm rounded-md"
          // placeholder="you@example.com"
          // defaultValue="adamwathan"
          value={input}
          onChange={handleChangeInput}
          aria-invalid="true"
          aria-describedby="email-error"
        />
        {error && (
          <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
            <ExclamationCircleIcon
              className="h-5 w-5 text-red-500"
              aria-hidden="true"
            />
          </div>
        )}
      </div>
      {error && (
        <p className="mt-2 text-sm text-red-600 py-2" id="email-error">
          No es tan difícil
        </p>
      )}

      <button
        type="submit"
        className=" mt-2 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
      >
        Probar suerte
      </button>
    </form>
  );
};
